import { getObjectThrows } from "../suspense/ObjectPreviews";
export function filterNonEnumerableProperties(properties) {
  // See https://static.replay.io/protocol/tot/Pause/#type-PropertyConfigurationFlags
  return properties.filter(property => property.flags == null || property.flags & 4);
}
export function mergePropertiesAndGetterValues(properties, getterValues, maxEntries = Infinity) {
  const trackedNames = new Set();
  const mergedProperties = [];

  for (let index = 0; index < properties.length; index++) {
    const property = properties[index];

    if (mergedProperties.length >= maxEntries) {
      return [mergedProperties, true];
    }

    trackedNames.add(property.name);
    mergedProperties.push(property);
  }

  for (let index = 0; index < getterValues.length; index++) {
    if (mergedProperties.length >= maxEntries) {
      return [mergedProperties, true];
    }

    const getterValue = getterValues[index];

    if (!trackedNames.has(getterValue.name)) {
      mergedProperties.push(getterValue);
    }
  }

  return [mergedProperties, false];
}

function isProtocolProperty(valueOrProperty) {
  return valueOrProperty.hasOwnProperty("get") || valueOrProperty.hasOwnProperty("isSymbol") || valueOrProperty.hasOwnProperty("set");
} // TODO It would be nice if the protocol's Value objects used a consistent format.
// As it is, these values have conditional fields which require special handling.
// This utility function maps from one ot the other.
//
// See https://linear.app/replay/issue/BAC-1808


export function protocolValueToClientValue(pauseId, protocolValue) {
  const name = protocolValue.hasOwnProperty("name") ? protocolValue.name : null;

  if (protocolValue.uninitialized || protocolValue.unavailable) {
    return {
      name,
      preview: protocolValue.uninitialized ? "(uninitialized)" : "(unavailable)",
      type: "string"
    };
  }

  if (protocolValue.hasOwnProperty("value")) {
    const value = protocolValue.value;

    switch (typeof value) {
      case "boolean":
        return {
          name,
          preview: `${value}`,
          type: "boolean"
        };

      case "number":
        return {
          name,
          preview: `${value}`,
          type: "number"
        };

      case "string":
        return {
          name,
          preview: value,
          type: "string"
        };

      case "object":
        return {
          name,
          preview: "null",
          type: "null"
        };
    }
  } else if (protocolValue.hasOwnProperty("bigint")) {
    return {
      name,
      preview: `${protocolValue.bigint}n`,
      type: "bigint"
    };
  } else if (protocolValue.hasOwnProperty("unserializableNumber")) {
    if (protocolValue.unserializableNumber === "NaN") {
      return {
        name,
        preview: "NaN",
        type: "nan"
      };
    } else {
      return {
        name,
        preview: `${protocolValue.unserializableNumber}`,
        type: "number"
      };
    }
  } else if (protocolValue.hasOwnProperty("symbol")) {
    return {
      name,
      preview: `${protocolValue.symbol}`,
      type: "symbol"
    };
  } else {
    let objectId;

    if (protocolValue.hasOwnProperty("object")) {
      objectId = protocolValue.object;
    } else {
      if (isProtocolProperty(protocolValue)) {
        if (protocolValue.hasOwnProperty("get")) {
          objectId = protocolValue.get;
        } else if (protocolValue.hasOwnProperty("set")) {
          objectId = protocolValue.set;
        }
      }
    }

    if (objectId) {
      const object = getObjectThrows(pauseId, objectId);
      const className = object.className;
      let preview;
      let type = "object";

      switch (className) {
        case "Array":
        case "BigInt64Array":
        case "BigUint64Array":
        case "Float32Array":
        case "Float64Array":
        case "Int8Array":
        case "Int16Array":
        case "Int32Array":
        case "Uint8Array":
        case "Uint8ClampedArray":
        case "Uint16Array":
        case "Uint32Array":
          type = "array";
          break;

        case "Date":
          type = "date";
          break;

        case "Function":
          type = "function";
          break;

        case "Map":
        case "WeakMap":
          type = "map";
          break;

        case "RegExp":
          type = "regexp";
          break;

        case "Set":
        case "WeakSet":
          type = "set";
          break;

        default:
          if (className.endsWith("Error") && object.preview?.properties?.find(property => property.name === "message")) {
            type = "error";
          } else if (className.startsWith("HTML")) {
            type = "html-element";
          } else if (className === "Text") {
            type = "html-text";
          }

          break;
      }

      return {
        name,
        objectId,
        preview,
        type
      };
    } // Tricky: Values representing undefined don't have explicit "value" keys
    // You have to detect them by the absence of an explicit "value" key–
    // Meaning they won't have "value", "bigint", "symbol", or "unserializableNumber" keys.
    // This is pretty awkward to work with and I wish the protocol would change.


    const keys = Object.keys(protocolValue).filter(key => key !== "flags" && key !== "name" && key !== "isSymbol");

    if (keys.length === 0) {
      return {
        name,
        preview: "undefined",
        type: "undefined"
      };
    }
  }

  console.error("Unsupported value type:", protocolValue);
  throw Error(`Unsupported value type`);
}
export function primitiveToClientValue(value) {
  let type = "object";
  let preview;

  switch (typeof value) {
    case "bigint":
      preview = `${value}n`;
      type = "bigint";
      break;

    case "boolean":
      preview = `${value}`;
      type = "boolean";
      break;

    case "function":
      preview = `${value}`;
      type = "function";
      break;

    case "number":
      if (Number.isNaN(value)) {
        preview = "NaN";
        type = "nan";
      } else {
        preview = `${value}`;
        type = "number";
      }

      break;

    case "object":
      if (value === null) {
        preview = "null";
        type = "null";
        break;
      } else if (Array.isArray(value)) {
        preview = `[${value}]`;
        type = "array";
        break;
      } else {
        const name = value.constructor.name;

        switch (name) {
          case "Map":
          case "WeakMap":
            preview = `${name}()`;
            type = "map";
            break;

          case "RegExp":
            preview = `${name}()`;
            type = "regexp";
            break;

          case "Set":
          case "WeakSet":
            preview = `${name}()`;
            type = "set";
            break;

          case "Text":
            type = "html-text";
            break;

          default:
            if (name?.startsWith("HTML")) {
              preview = `<${name} />`;
              type = "html-element";
            } else {
              preview = "Object";
            }

            break;
        }
      }

      break;

    case "string":
      preview = value;
      type = "string";
      break;

    case "symbol":
      preview = value.toString();
      type = "symbol";
      break;

    case "undefined":
      preview = "undefined";
      type = "undefined";
      break;
  }

  return {
    name: null,
    preview,
    type
  };
}
export function protocolSourcesToSourceTree(sources) {
  const sourceTree = [];
  let protocol = null;
  let origin = null;
  sources.forEach(source => {
    if (!source.url) {
      return;
    }

    let url;

    try {
      url = new URL(source.url);
    } catch (error) {
      return;
    }

    if (url.protocol == "replay-content:") {
      return;
    }

    if (url.protocol != "replay-content:" && url.protocol != "https:" && url.protocol !== protocol) {
      sourceTree.push({
        type: "protocol",
        protocol: url.protocol
      });
      protocol = url.protocol;
    }

    if (url.origin != null && url.origin !== origin) {
      sourceTree.push({
        type: "origin",
        origin: url.origin
      });
      origin = url.origin;
    }

    if (url.pathname) {
      sourceTree.push({
        type: "source",
        source: source,
        path: url.pathname
      });
    }
  });
  return sourceTree;
}