import { useCallback, useLayoutEffect, useState, useTransition } from "react";
import { localStorageGetItem, localStorageSetItem } from "../utils/storage"; // Forked from https://usehooks.com/useLocalStorage/

export default function useLocalStorage(key, initialValue, scheduleUpdatesAsTransitions = false) {
  const {
    0: isPending,
    1: startTransition
  } = useTransition();
  const {
    0: value,
    1: setValue
  } = useState(() => {
    const storedValue = localStorageGetItem(key);

    if (storedValue != null) {
      return JSON.parse(storedValue);
    } else {
      return initialValue;
    }
  });
  const setValueWrapper = useCallback(action => {
    if (scheduleUpdatesAsTransitions) {
      startTransition(() => {
        setValue(action);
      });
    } else {
      setValue(action);
    }
  }, [scheduleUpdatesAsTransitions]); // Listen for changes to this local storage value made from other windows.

  useLayoutEffect(() => {
    const onStorage = () => {
      const newValue = localStorageGetItem(key);

      if (newValue != null) {
        startTransition(() => {
          setValue(JSON.parse(newValue));
        });
      }
    };

    window.addEventListener("storage", onStorage);
    return () => {
      window.removeEventListener("storage", onStorage);
    };
  }, [key]); // Sync changes to local storage

  useLayoutEffect(() => {
    const string = JSON.stringify(value);
    localStorageSetItem(key, string);
  }, [key, value]);
  return [value, setValueWrapper, isPending];
}